* {
  margin: 0;
  padding: 0;
}
body {
  display: flex;
  justify-content: center;
}

.one {
  border: 1px solid rgb(221, 212, 212);
  background-color: rgb(175, 142, 51);
  /* height: 656px; */
}
.two {
  border: 1px solid rgb(194, 99, 99);
  background-color: brown;
  /* height: 50px; */
  /* width: 400px; */
}
.two-box {
  border: 1px solid black;
  background-color: black;
}
.three {
  border: 1px solid rgb(0, 255, 21);
  background-color: darkgreen;
  /* height: ; */
}
.five {
  border: 1px solid rgb(255, 0, 76);
  background-color: rgb(100, 0, 83);
}
.four {
  border: 1px solid rgb(0, 60, 255);
  background-color: blue;

  /* height: 160px; */
  /* width: 400px; */
}
.container {
  background-color: aqua;
  width: 1200px;
  display: grid;

  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 500px 10px 1px;

  /* height: 650px; */
}
